'use client'

import { user } from "@/api/user";
import LeftMenu from "@/components/LeftMenu/LeftMenu";
import { useEffect, useState } from "react";

export default function Home() {

  const [userEmail, setUserEmail] = useState('')
  const [userPassword, setUserPassword] = useState('')
  const [data, setData] = useState({})

  
  const onClickBtn = (e: { preventDefault: () => void; }) => {
    e.preventDefault()
    user(userEmail, userPassword)
      .then((res) => {
        setData(res)
      })
  }
  
  useEffect(() => {
    console.log(data)
  }, [data])

  return (
    <div className="grid items-center justify-start items-center min-h-screen font-[family-name:var(--font-geist-sans)]">
      <main className="flex flex-col gap-[32px] row-start-2 items-center sm:items-start">
        <div>
          <LeftMenu/>
        </div>
      </main>
      <footer className="row-start-3 flex items-center justify-center">
        SBI
      </footer>
    </div>
  );
}
